<template>
	<div id="navigation">
		<div id="header">
			<div id="brand">
				<div id="font">WOPELO</div>
			</div>
			<div id="news">
				<div id="bell">
					<span class="icon-bell"></span>
				</div>
			</div>
		</div>
		<div id="list">
			<div class="option">
				<router-link to="/backstage/saying"></router-link>
			</div>
			<div class="option">
				<router-link to="/backstage/blog"></router-link>
			</div>
			<div class="option">
				<router-link to="/backstage/type"></router-link>
			</div>
			<div class="option">
				<router-link to="/backstage/manage"></router-link>
			</div>
			<div class="option">
				<router-link to="/backstage/comment"></router-link>
			</div>
			<div class="option">
				<router-link to="/backstage/editor"></router-link>
			</div>
			<div class="option">
				<router-link to="/backstage/about"></router-link>
			</div>
			<div class="option" id="top">
				<a v-on:click="backTop"></a>
			</div>
		</div>
		<div id="edit">
			<div id="view">
				<router-view></router-view>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				code:""
			}
		},
		created(){
			//
		  	let admin=sessionStorage.getItem("admin");
		  	let code=sessionStorage.getItem("code");
			if(!admin||!code){
				this.$router.push({path:'/backstage/'});
			}else{
				this.checkCode(code);
			}
		},
		methods:{
			checkCode(num){
				this.$axios({
					method:"post",
					url:"/api/check",
					data:{
						code:num
					}
				}).then((res)=>{
					console.log(res.data);
					if(res.data.error){
						this.$router.push({path:'/backstage/'});
					}else{
						console.log("code");
						this.code=num;
					}
				})
			},
			backTop(){
				document.documentElement.scrollTop=0;
			}
		}
	}
</script>

<style lang="scss" rel="text/css">
	@import './navigation.scss';
</style>


